@import '~@avtopro/style';

.pro-checkbox-mixin(@iconMargin: 0.6rem) {
    @size: unit(16 / @baseFontSize, rem);

    --private_pro-checkbox-color: var(--pro-checkbox-color, @grayLightestColor);
    --private_pro-checkbox-flag-color: var(
        --pro-checkbox-flag-color,
        var(--pro-checkbox-color, @blackLightColor)
    );
    --private_pro-checkbox-background-color: var(
        --pro-checkbox-background-color,
        @whiteColor
    );

    position: relative;
    display: block;
    font-size: 1rem;
    /* TODO: проверить нужен ли line-height, и если нужен, то проверить <CheckboxOption> */
    /*line-height: @size;*/
    padding-left: @size + @iconMargin;
    text-indent: -@size - @iconMargin;
    color: var(--pro-form-control-color, @blackLightColor);

    &__icon {
        display: inline-block;
        vertical-align: text-bottom;
        width: @size;
        height: @size;
        margin-right: @iconMargin;
        text-indent: 0;
        pointer-events: none;
        user-select: none;

        &__frame {
            fill: var(--private_pro-checkbox-background-color);
            stroke: var(--private_pro-checkbox-color);
        }

        &__flag {
            fill: none;
            stroke: none;
        }

        input:checked + & &__flag {
            stroke: var(--private_pro-checkbox-flag-color);
        }

        input:disabled + & &__frame {
            --private_pro-checkbox-background-color: var(
                --pro-checkbox-disabled-background-color,
                var(--pro-checkbox-background-color, @whiteDarkestColor)
            );

            --private_pro-checkbox-color: var(
                --pro-checkbox-disabled-color,
                var(--pro-checkbox-color, @grayLightestColor)
            );
        }

        input:disabled:checked + & &__flag {
            --private_pro-checkbox-flag-color: var(
                --pro-checkbox-disabled-flag-color,
                var(
                    --pro-checkbox-disabled-color,
                    var(
                        --pro-checkbox-flag-color,
                        var(--pro-checkbox-color, @grayLightColor)
                    )
                )
            );
        }

        input:focus + &,
        input:active + & {
            outline: none;
            box-shadow: 0 0 0 1px @whiteColor, 0 0 4px 1px @blueLighterColor;
            border-radius: 2px;
        }
    }

    input {
        position: absolute;
        opacity: 0;
        z-index: -1;
        -webkit-appearance: none;
    }
}

.pro-checkbox {
    .pro-checkbox-mixin();
}

@theme: default;