@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/font.less';

.pro-btn-mixin() {
    --box-in-height: var(--pro-btn-in-height);
    --box-out-height: var(--pro-btn-out-height);

    &:not(&--square) {
        --box-padding-x: var(--pro-btn-padding-x);
    }

    .box();

    font-size: var(--pro-btn-font-size, 1rem);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    white-space: nowrap;
    vertical-align: middle;
    user-select: none;
    border: none;
    cursor: pointer;
    overflow: hidden;
    -webkit-appearance: none;

    &[disabled] {
        cursor: default;
    }
}

/*
    DO NOT USE
    Обратная совместимость для старого формата
    вызова pro-btn-mixin с аргументами
*/
.pro-btn-mixin(@fontSize) {
    .pro-btn-mixin();
    .buttonFontSize(@fontSize);
}

/*
    DO NOT USE
    Обратная совместимость для старого формата
    вызова pro-btn-mixin с аргументами
*/
.pro-btn-mixin(@fontSize; @outHeight) {
    .pro-btn-mixin(@fontSize);
    --pro-btn-out-height: unit(@outHeight / @baseFontSize, rem);
}

/*
    DO NOT USE
    Обратная совместимость для старого формата
    вызова pro-btn-mixin с аргументами
*/
.pro-btn-mixin(@fontSize; @outHeight; @hPadding) {
    .pro-btn-mixin(@fontSize; @outHeight);
    --pro-btn-padding-x: unit(@hPadding / @baseFontSize, rem);
}

.colorizeButton(@normal; @hover; @active; @disabled) {
    a&,
    & {
        color: @normal[color];
        background: @normal[background];
        border-color: @normal[border-color];

        &:hover {
            color: @hover[color];
            background: @hover[background];
            border-color: @hover[border-color];
        }

        &:active {
            color: @active[color];
            background: @active[background];
            border-color: @active[border-color];
        }

        &:disabled,
        &[disabled] {
            color: @disabled[color];
            background: @disabled[background];
            border-color: @disabled[border-color];
        }
    }
}

.buttonFontSize(@fs) {
    --pro-btn-font-size: unit(@fs / @baseFontSize, rem);
    // prettier-ignore
    --pro-btn-in-height: unit(@fontSizes[$@fs] / @baseFontSize, rem);
}

@theme: default;