@import '~@avtopro/style';
@import '~@avtopro/style/mixins/font.less';
@import '~@avtopro/style/mixins/media.less';

.pro-placeholder {
    .font(14);
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    margin: 20px 0;

    @media (max-width: @screen-sm) {
        .font(13);
    }

    & > * {
        margin-bottom: unit(10 / @baseFontSize, rem);

        &:last-child {
            margin-bottom: 0;
        }
    }
}

.pro-placeholder__header {
    .font(20);
    margin-bottom: unit(5 / @baseFontSize, rem);

    &:last-child {
        margin-bottom: 0;
    }

    &--bold {
        font-weight: 900;
    }
}

.pro-placeholder__robo {
    .robo-mixin(@backgroundImage; @height: 182px) {
        height: @height;
        width: 100%;
        margin: unit(25 / @baseFontSize, rem) 0 unit(15 / @baseFontSize, rem);
        background-repeat: no-repeat;
        background-position: 50%;
        background-image: @backgroundImage;

        @media (max-width: @screen-sm) {
            height: @height / 1.3;
            margin-bottom: unit(10 / @baseFontSize, rem);
        }

        &:first-child {
            margin-top: 0;
        }

        &:last-child {
            margin-bottom: 0;
        }
    }

    & {
        .robo-mixin(url('../icons/robot/robot.svg'));
    }

    &--no-results {
        .robo-mixin(url('../icons/robot/robo_status_noresults.svg'));
    }

    &--no-ff-results {
        .robo-mixin(url('../icons/robot/robot-ff-zero.svg'));
    }

    &--no-shops {
        .robo-mixin(url('../icons/robot/no_shops.svg'));
    }

    &--no-warehouses {
        .robo-mixin(url('../icons/robot/robo_no_warehouses.svg'));
    }

    &--del-message {
        .robo-mixin(url('../icons/robot/deleted_message.svg'));
    }

    &--no-messages {
        .robo-mixin(url('../icons/robot/no_messages.svg'));
    }

    &--no-reviews {
        .robo-mixin(url('../icons/robot/robot-zero.svg'));
    }

    &--lamp {
        .robo-mixin(url('../icons/robot/robo-lamp.svg'));
    }

    &--no-sto {
        .robo-mixin(url('../icons/robot/no_sto.svg'));
        background-size: contain;
    }

    &--no-sto-youtube {
        .robo-mixin(url('../icons/robot/no_sto-youtube.svg'));
        background-size: contain;
    }

    &--youtube {
        .robo-mixin(url('../icons/robot/youtube.svg'));
        background-size: contain;
    }

    &--service-unavailable {
        .robo-mixin(url('../icons/robot/service_unavailable.svg'));
        background-size: contain;
    }

    &--no-orders {
        .robo-mixin(url('../icons/robot/no_orders.svg'));
        background-size: contain;
    }

    &--no-banner {
        .robo-mixin(url('../icons/robot/no_banner.svg'));
        background-size: contain;
    }

    &--loader {
        .robo-mixin(
            @backgroundImage: url('../icons/robot/robot_preloader.svg') ;
                @height: 255px
        );
    }
}

@theme: default;