@import '../themes/index.less';

/*
    Добавляет скругление углов первому и последнему соседнему элементу.
*/
.glue(border-radius) {
    &:first-child {
        border-top-left-radius: @borderRadius;
        border-bottom-left-radius: @borderRadius;
    }

    &:last-child {
        border-top-right-radius: @borderRadius;
        border-bottom-right-radius: @borderRadius;
    }
}

/* 
    Спрямляет углы соседних элементов в местах их соприкосновения.
*/
.glue(border-radius-reset) {
    &:not(:first-child) {
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    &:not(:last-child) {
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }
}

/*
    Убирает вертикальные границы соседних элементов.
*/
.glue(border) {
    &:not(:first-child) {
        border-left: none;
    }

    &:not(:last-child) {
        border-right: none;
    }
}

/* Обратная совместимость. */
.glue() {
    .glue(border-radius);
}

@theme: default;