@import '../themes/index.less';

/* Устанавливает размер шрифта и высоту строки по умолчанию */
.font() {
    .font(unit(@baseFontSize, px));
}

/* Применяет высоту строки, соответствующую высоте шрифта */
.font(@fs) when ((ispixel(@fs)) or not (get-unit(@fs))) {
    @fsn: unit(@fs);
    .fontSize(@fs);
    .lineHeight(
        @fsn;
        @fontSizes[$@fsn]
    );
}

/* Для исключительных комбинаций размера шрифта и высоты строки */
.font(@fs; @lh) {
    .fontSize(@fs);

    line-height: @lh;
}

.fontSize(@fs) when(@fs < @baseFontSize) {
    --p-margin-bottom: unit(10 / @baseFontSize, rem);
}

.fontSize(@fs) when ((ispixel(@fs)) or (isem(@fs))) {
    font-size: @fs;
}

.fontSize(@fs) when (ispercentage(@fs)) {
    font-size: unit((@fs / 100 * @baseFontSize), rem);
}

/* prettier-ignore */
.fontSize(@fs) when (not (ispixel(@fs)) and not (isem(@fs)) and not (ispercentage(@fs))) {
    font-size: unit((@fs / @baseFontSize), rem);
}

.lineHeight(@fs; @lh) {
    line-height: unit(@lh / @fs);
}

@theme: default;