@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/glue.less';
@import '~@avtopro/style/mixins/nowrap.less';
@import '~@avtopro/style/mixins/bordering.less';
@import './options/index.less';

.pro-select {
    // Наружная высота блока без учёта границы
    --pro-select-height: unit(@baseInputHeight / @baseFontSize, rem);
    // Горизонтальные поля блока - от края до внутреннего контента
    --pro-select-padding-x: unit(15 / @baseFontSize, rem);
    // Толщина прозрачной границы, которая добавляется опциям
    // для увеличения их размера по вертикали:
    // высота опции по макету = 1 + 28 + 1 = 30
    --pro-select-option-border-width: 1px;
    --pro-select-placeholder-color: var(
        --pro-form-control-color,
        @blackLightColor
    );
    --pro-select-options-color: @blackLightColor;

    position: relative;
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    font-size: 1rem;
    min-height: var(--pro-select-height);

    /* Важно! Помогает обрезать текст, см. https://css-tricks.com/flexbox-truncated-text/ */
    min-width: 0;
    color: @grayColor;
    background: @whiteColor;
    user-select: none;

    &--sm {
        --pro-select-height: unit(@compactInputHeight / @baseFontSize, rem);
        --pro-select-padding-x: unit(10 / @baseFontSize, rem);
    }

    &--list {
        height: auto;
    }

    &--framed {
        .bordering();
    }

    &--disabled {
        --pro-select-placeholder-color: inherit;
        --pro-select-options-color: inherit;

        color: @grayLightColor;
        background: @whiteDarkestColor;
        pointer-events: none;
    }

    &--transparent,
    &--framed&--transparent {
        background: none;
        border-color: transparent;
    }

    &:focus,
    & :focus {
        outline: none;
    }

    &[data-value=''] {
        --pro-select-placeholder-color: var(--pro-form-control-color, inherit);
    }
}

.pro-select__group {
    position: relative;
    display: flex;
    flex-grow: 1;
    width: 100%;
}

/* Двухклассовый селектор нужен только чтобы перебить Bootstrap. Удалить после откл. старых стилей */
.pro-select .pro-select__search,
.pro-select .pro-select__placeholder {
    .glue();

    flex: 1 1 auto;
    margin: 0;
    font-family: inherit;
}

.pro-select .pro-select__search {
    --box-out-height: var(--pro-select-height);
    --box-padding-x: var(--pro-select-padding-x);
    .box();

    display: block;
    width: 1%;
    appearance: none;
    color: @blackLightColor;

    /* Перебить Bootstrap */
    border: none !important;
    box-shadow: none !important;
    transition: none !important;
}

.pro-select .pro-select__placeholder {
    .nowrap();
    // У вставленных в placeholder опций убирается граница,
    // т. к. теперь опция обведена рамкой самого pro-select
    --pro-select-option-border-width: 0;

    display: flex;
    flex-wrap: wrap;
    // В плейсхолдере может быть массив элементов, таких как Label,
    // нужно чтобы между ними был зазор
    gap: 1px;
    // align-content: center; // Плохо работает с множественным выбором
    align-items: center; // safari fix
    padding: 0;
    color: var(--pro-select-placeholder-color);
    cursor: default;
}

.pro-select__toggle {
    @iconHeight: unit(@baseIconHeight / @baseFontSize, rem);

    display: flex;
    flex: 0 0 auto;
    align-items: center;
    width: @iconHeight;
    height: @iconHeight;
    padding: calc((var(--pro-select-height) - @iconHeight) / 2);
    border: none;
    box-sizing: content-box;
    text-align: center;
    white-space: nowrap;
    color: inherit;
    cursor: pointer;
}

/*
    TODO: стиль "уменьшенной" toggle-кнопки был создан раньше,
    чем появилась "sm"-модификация селекта и не особо согласуется с ней.
    Следует удалить этот модификатор, заменив его на свойство --pro-select-toggle-width,
    или что-то подобное для произвольной модификации размера кнопки.
*/
.pro-select__toggle--small {
    padding-left: unit(5 / @baseFontSize, rem);
    padding-right: unit(5 / @baseFontSize, rem);
}

/* TODO: этот класс нужен только на Автопро, отсюда его нужно убрать */
.pro-select__numbers {
    &--highlight {
        font-weight: bold;
    }

    span {
        color: @grayColor;
    }
}

.pro-select__placeholder .pro-select__numbers {
    font-weight: 400;
}

.pro-select__checkbox-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 1px;
    padding: 10px var(--pro-select-padding-x) 12px;
    background: @whiteColor;

    a {
        width: 100%;
        margin-top: 10px;
    }
}

.pro-select .pro-label-block {
    --box-out-height: calc(var(--pro-select-height) - 2px);
}

.pro-select.pro-select--opened {
    .pro-select__toggle__arrow {
        transform: rotate(180deg);
    }
}

@theme: default;