@import '~@avtopro/style/mixins/box.less';

@import '../../mixins.less';

.pro-input {
    font-family: inherit;

    /* Селектор [type] нужен только чтобы перебить стили Bootstrap
     * Можно будет удалить после отключения старых стилей */
    input[type]&,
    textarea& {
        --box-out-height: unit(@baseInputHeight / @baseFontSize, rem);
        --box-padding-x: unit(15 / @baseFontSize, rem);
        .pro-input-mixin();
        .box();
    }

    input[type='search']& {
        /* stylelint-disable-next-line property-no-vendor-prefix */
        -webkit-appearance: none;

        &::-webkit-search-decoration,
        &::-webkit-search-cancel-button {
            display: none;
        }
    }

    textarea& {
        resize: vertical;
        overflow-x: hidden;
        overflow-y: auto;

        &[rows] {
            height: auto;
        }
    }

    &--sm {
        input[type]&,
        textarea& {
            --box-out-height: unit(@compactInputHeight / @baseFontSize, rem);
            --box-padding-x: unit(10 / @baseFontSize, rem);
        }
    }

    &--error {
        border-color: @redColor;
    }
}

@theme: default;