/* Селект с запчастями */
/* TODO: нужен ли, или устарел после редизйна складов? */
.pro-select__part-option {
    height: 46/13rem;
    display: flex;

    .part-option__info {
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        font-size: 1rem;
        line-height: 1rem;
        color: @grayColor;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        & > div {
            color: @blackLightColor;
        }
    }

    .part-option__image {
        min-width: 64px;
        height: 44/13rem;
        border: 1px solid @whiteDarkestColor;
        margin-right: 10px;
        box-sizing: content-box;

        & > div {
            background-position: center center;
            background-size: cover;
            background-origin: content-box;
            border: 2px solid @whiteColor;
            width: 100%;
            height: 100%;
            box-sizing: border-box;
        }
    }
}

@theme: default;