@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/nowrap.less';

.pro-select__color-option {
    --box-padding-x: var(--pro-select-padding-x);
    .box(@outHeight: @compactInputHeight);
    position: relative;
    display: flex;
    align-items: center;
    border: solid transparent;
    border-width: var(--pro-select-option-border-width, 0) 0;

    & > :first-child {
        // Превью позиционируется абсолютно, чтобы своей высотой не растягивать строку
        // нарушая выравнивание селекта относительно соседних элементов
        position: absolute;
        width: unit(@baseIconHeight / @baseFontSize, rem);
        height: unit(@baseIconHeight / @baseFontSize, rem);
        box-sizing: border-box;
        border: 2px solid @whiteColor;
        outline: 1px solid @whiteDarkestColor;
    }

    & > :last-child {
        .nowrap();
        margin-left: unit((@baseIconHeight + 10) / @baseFontSize, rem);
    }
}

@theme: default;