@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/nowrap.less';
@import '~@avtopro/style/mixins/reset.less';

.pro-data-list {
    --pro-data-list-group-height: unit(@baseInputHeight / @baseFontSize, rem);
    --pro-data-list-group-padding-x: unit(15 / @baseFontSize, rem);

    .reset-ul();
    border: none;

    &:not(:first-child) {
        border-top: 1px solid @whiteDarkestColor;
        padding-top: unit(10 / @baseFontSize, rem);
        margin-top: unit(10 / @baseFontSize, rem);
    }

    &--sm {
        --pro-data-list-group-height: unit(
            @compactInputHeight / @baseFontSize,
            rem
        );
        --pro-data-list-group-padding-x: unit(10 / @baseFontSize, rem);
    }
}

.pro-data-list[data-group]:not([data-group='']) {
    padding-top: 0;

    &::before {
        --box-out-height: var(--pro-data-list-group-height);
        --box-padding-x: var(--pro-data-list-group-padding-x);
        .box();
        content: attr(data-group);
        height: auto;
        display: block;
        font-weight: 700;
    }
}

.pro-data-list__option {
    .nowrap();
    cursor: pointer;

    &:hover {
        background: @whiteDarkColor;
    }

    &--autoheight {
        white-space: normal;
    }

    &--selected,
    &--selected:hover {
        background: @whiteDarkerColor;
    }
}

@theme: default;