@import '../themes/index.less';

.bordering(
    @focus: false;
    @borderWidth: 1px;
    @borderStyle: solid;
    @borderColor: @grayLightestColor
)
    when
    (@focus = false) {
    border-width: var(--bordering-border-width, @borderWidth);
    border-style: var(--bordering-border-style, @borderStyle);
    border-color: var(--bordering-border-color, @borderColor);
    border-radius: var(--bordering-border-radius, @borderRadius);
    box-shadow: none;

    .bordering(@focus: true);
}

.bordering(@focus; @borderColor: @grayLightColor) when (@focus = true) {
    &:focus,
    &:focus-within {
        border-color: var(
            --bordering-border-focus-color,
            var(--bordering-border-color, @borderColor)
        );
    }
}

@theme: default;