@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/nowrap.less';

.pro-select__option {
    --box-padding-x: var(--pro-select-padding-x);
    .box(@outHeight: @compactInputHeight);
    .nowrap();
    border: solid transparent;
    border-width: var(--pro-select-option-border-width, 0) 0;

    &--autoheight {
        height: auto !important;
        white-space: normal;
    }
}

.pro-select__option-emph {
    font-weight: 700;
    color: @blackColor;
}

// Освобождаем место под текст опции, если она выбрана (вставлена в placeholder)
// и за ней идёт стрелочка разворачивания поиска, у которой есть свои поля
.pro-select,
.pro-select--mobile {
    .pro-select__placeholder:not(:last-child) .pro-select__option {
        padding-right: 0;
    }
}

@theme: default;