@import '../themes/index.less';

/* Устанавливает стандартную ширину, высоту и отступы элементов форм
 * По умолчанию отступы со всех сторон одинаковые */
.box(@inHeight: 19; @outHeight: @baseInputHeight; @hPadding: auto) {
    @boxInHeightUnit: var(
        --box-in-height,
        unit(@inHeight / @baseFontSize, rem)
    );
    @boxOutHeightUnit: var(
        --box-out-height,
        unit(@outHeight / @baseFontSize, rem)
    );
    @boxPaddingXUnit: var(
        --box-padding-x,
        calc((@boxOutHeightUnit - @boxInHeightUnit) / 2)
    );
    @boxPaddingYUnit: var(
        --box-padding-y,
        calc((@boxOutHeightUnit - @boxInHeightUnit) / 2)
    );

    box-sizing: content-box;
    height: @boxInHeightUnit;
    line-height: @boxInHeightUnit;
    min-height: @boxInHeightUnit;
    min-width: @boxInHeightUnit;
    padding-top: @boxPaddingYUnit;
    padding-bottom: @boxPaddingYUnit;
    padding-left: @boxPaddingXUnit;
    padding-right: @boxPaddingXUnit;
}

/* Устанавливает отступы слева и справа */
.box(@inHeight: auto; @outHeight: auto; @hPadding) when (@hPadding > 0) {
    @boxPaddingXUnit: var(
        --box-padding-x,
        unit(@hPadding / @baseFontSize, rem)
    );
    padding-left: @boxPaddingXUnit;
    padding-right: @boxPaddingXUnit;
}

@theme: default;