/* stylelint-enable prettier/prettier */
/* Устанавливает стандартную ширину, высоту и отступы элементов форм
 * По умолчанию отступы со всех сторон одинаковые */
/* Устанавливает отступы слева и справа */
/* Устанавливает размер шрифта и высоту строки по умолчанию */
/* Применяет высоту строки, соответствующую высоте шрифта */
/* Для исключительных комбинаций размера шрифта и высоты строки */
/* prettier-ignore */
/*
    Добавляет скругление углов первому и последнему соседнему элементу.
*/
/* 
    Спрямляет углы соседних элементов в местах их соприкосновения.
*/
/*
    Убирает вертикальные границы соседних элементов.
*/
/* Обратная совместимость. */
.pro-input {
  font-family: inherit;
  /* Селектор [type] нужен только чтобы перебить стили Bootstrap
     * Можно будет удалить после отключения старых стилей */
}
input[type].pro-input,
textarea.pro-input {
  --box-out-height: 3.23076923rem;
  --box-padding-x: 1.15384615rem;
  font-size: 1rem;
  display: block;
  flex: 1 1 auto;
  /* Баг с этим width: auto - поле поиска не вписывается в 320px */
  margin: 0;
  border: none;
  overflow: hidden;
  color: var(--pro-form-control-color, #3b3b3b);
  background-color: #ffffff;
  outline: 0;
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
}
input[type].pro-input:disabled,
textarea.pro-input:disabled {
  background-color: #ededed;
}
input[type].pro-input::placeholder,
textarea.pro-input::placeholder {
  color: var(--pro-form-control-color, #7b7b7b);
}
input[type].pro-input--framed,
textarea.pro-input--framed {
  border-width: var(--bordering-border-width, 1px);
  border-style: var(--bordering-border-style, solid);
  border-color: var(--bordering-border-color, #d8d8d8);
  border-radius: var(--bordering-border-radius, 3px);
  box-shadow: none;
}
input[type].pro-input--framed:focus,
textarea.pro-input--framed:focus,
input[type].pro-input--framed:focus-within,
textarea.pro-input--framed:focus-within {
  border-color: var(--bordering-border-focus-color, var(--bordering-border-color, #a6a6a6));
}
input[type].pro-input--framed:first-child,
textarea.pro-input--framed:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
input[type].pro-input--framed:last-child,
textarea.pro-input--framed:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
input[type].pro-input:focus,
textarea.pro-input:focus {
  outline: none;
}
input[type='search'].pro-input {
  /* stylelint-disable-next-line property-no-vendor-prefix */
  -webkit-appearance: none;
}
input[type='search'].pro-input::-webkit-search-decoration,
input[type='search'].pro-input::-webkit-search-cancel-button {
  display: none;
}
textarea.pro-input {
  resize: vertical;
  overflow-x: hidden;
  overflow-y: auto;
}
textarea.pro-input[rows] {
  height: auto;
}
input[type].pro-input--sm,
textarea.pro-input--sm {
  --box-out-height: 2.15384615rem;
  --box-padding-x: 0.76923077rem;
}
.pro-input--error {
  border-color: #e85252;
}
