@import '~@avtopro/style';
@import '~@avtopro/style/mixins/font.less';
@import '~@avtopro/style/mixins/media.less';
@import '~@avtopro/style/mixins/svg-embedded.less';

/* Базовая вёрстка модального окна.
 * Для отображения поверх других элементов
 * модальное окно должно быть обёрнуто в .overlay
 */
.modwin {
    @vPadding: 25px;
    @hPadding: 45px;

    --modwin-margin: 15px;
    --modwin-v-padding: @vPadding;
    --modwin-h-padding: @hPadding;
    --modwin-border-radius: @borderRadius;

    display: flex;
    flex-direction: column;
    position: relative;
    width: 500px;
    min-width: @screen-min;
    box-sizing: border-box;
    margin: var(--modwin-margin);
    padding: @vPadding @hPadding; // IE11 & others legacy fallback
    padding: var(--modwin-v-padding) var(--modwin-h-padding);
    border-radius: var(--modwin-border-radius);
    background-color: @whiteColor;

    @media (max-width: @modwinBreakMobile) {
        .modwin--fullscreen();

        --modwin-h-padding: 15px;
    }

    .pro-loader {
        border-radius: var(--modwin-border-radius);
    }
}

/* Модал увеличенной ширины */
.modwin--wide {
    width: 100%;
    max-width: 700px;
}

/* Модал ещё более увеличенной ширины
   (что-то много развелось модалов, надо с Русланом переговорить).
*/
.modwin--wider {
    width: 100%;
    max-width: 800px;
}

/* Модальное окно, занимающее всю площадь экрана */
.modwin--fullscreen {
    --modwin-margin: 0;
    --modwin-border-radius: 0;

    width: 100% !important;
    height: 100% !important;
    max-width: auto;
    overflow: auto;
}

/* Модальное окно без отступов. */
.modwin--no-pad {
    --modwin-v-padding: 0;
    --modwin-h-padding: 0;
}

/* Обёртка для содержимого модала (главным образом нужна для правильного определения first- и last-child */
.modwin__content {
    flex-grow: 1;

    & > :first-child {
        border-top-left-radius: var(--modwin-border-radius);
        border-top-right-radius: var(--modwin-border-radius);
    }

    & > :last-child {
        border-bottom-left-radius: var(--modwin-border-radius);
        border-bottom-right-radius: var(--modwin-border-radius);
    }
}

/* Заголовок модального окна */
.modwin__caption {
    .font(20);

    font-family: inherit;
    font-weight: 900;
    width: 100%;
    margin: 5px 0 20px;
    padding: 0 10px;
    box-sizing: border-box;
    text-align: center;
    color: @blackColor;
}

/* Уменьшенный заголовок модального окна */
.modwin__mid-caption {
    .font(16);

    font-family: inherit;
    font-weight: 900;
    width: 100%;
    margin: 15px 0 10px;
    padding: 0 10px;
    box-sizing: border-box;
    text-align: center;
    color: @blackColor;
}

/* Подзаголовок модального окна */
.modwin__sub-caption {
    .font(14);

    width: 100%;
    margin: 0 0 20px;
    padding: 0 10px;
    box-sizing: border-box;
    text-align: center;

    .modwin__caption + & {
        margin-top: -15px;
    }

    &--small {
        .font(13);

        margin-top: -5px;
    }
}

/* Элементы с выносом на поля модала вверх/вправо/вниз/влево */
.modwin__y-pull-top {
    margin-top: calc(-1 * var(--modwin-v-padding));
}

.modwin__x-pull-right {
    margin-right: calc(-1 * var(--modwin-h-padding));
}

.modwin__y-pull-bottom {
    margin-bottom: calc(-1 * var(--modwin-v-padding));
}

.modwin__x-pull-left {
    margin-left: calc(-1 * var(--modwin-h-padding));
}

/* Элементы с выносом на поля модала с обеих сторон */
.modwin__x-pull {
    .modwin__x-pull-left();
    .modwin__x-pull-right();
}

.modwin__y-pull {
    .modwin__y-pull-top();
    .modwin__y-pull-bottom();
}

/* Кнопка закрытия модала */
.modwin__close {
    display: block;
    height: unit(@baseIconHeight / @baseFontSize, rem);
    width: unit(@baseIconHeight / @baseFontSize, rem);
    position: absolute;
    top: unit(15 / @baseFontSize, rem);
    right: unit(15 / @baseFontSize, rem);
    cursor: pointer;
    color: @grayLighterColor;

    &:hover,
    &:focus {
        color: @grayColor;
    }

    svg {
        .svg-embedded();
    }
}

/* Горизонтальный разделитель блоков в модале */
.modwin__hr {
    .modwin__x-pull();

    flex: 1 0 100%;
    height: 1px;
    margin-top: 10px;
    margin-bottom: 10px;

    &::after {
        content: '';
        display: block;
        width: 100%;
        border-top: 1px solid @whiteDarkestColor;
    }
}

/* Блок с роботом и фоном на всю ширину модала */
.modwin__alert {
    .font(13);
    .modwin__x-pull();

    position: relative;
    margin-top: 25px;
    background-color: @skyBlueLighterColor;

    &:first-child {
        margin-top: calc(-1 * var(--modwin-v-padding));
    }

    &:last-child {
        margin-bottom: calc(-1 * var(--modwin-v-padding));
    }

    @media (min-width: @modwinBreakMobile + 0.001) {
        padding: 15px 55px 55px;

        &::after {
            left: 65px;
        }

        &::before {
            left: 55px;
        }
    }

    @media (max-width: @modwinBreakMobile) {
        padding: 15px 25px 55px;

        &::after {
            left: 35px;
        }

        &::before {
            left: 25px;
        }
    }

    &::after {
        content: '';
        display: block;
        width: 46px;
        height: 34px;
        background: url('../icons/robot/robot_info.svg') no-repeat;
        background-size: 100%;
        bottom: 0;
        position: absolute;
    }

    &--error::after {
        background-image: url('../icons/robot/robot_error.svg');
    }

    &--success::after {
        background-image: url('../icons/robot/robot_success.svg');
    }

    &--warning::after {
        background-image: url('../icons/robot/robot_warning.svg');
    }

    &::before {
        content: '';
        display: block;
        width: 54px;
        height: 44px;
        background: url('../icons/robot/robot-head-alt.svg') 50% 100% no-repeat;
        background-size: auto 100%;
        position: absolute;
        bottom: 0;
    }
}

/* Шапка модала с информером-роботом */
.modwin__head {
    .modwin__x-pull();

    height: 87px;
    margin-top: calc(-1 * var(--modwin-v-padding));
    margin-bottom: 25px;
    background: url('../icons/robot/robot-head-alt.svg') 50% 100% no-repeat;
    background-size: auto 100%;
    background-color: @skyBlueLighterColor;

    &::after {
        content: '';
        display: block;
        width: 100%;
        height: 100%;
        background: url('../icons/robot/robot_info.svg') 50% 100% no-repeat;
        background-size: auto 100%;
    }

    &--error::after {
        background-image: url('../icons/robot/robot_error.svg');
    }

    &--success::after {
        background-image: url('../icons/robot/robot_success.svg');
    }

    &--warning::after {
        content: '';
        background-image: url('../icons/robot/robot_warning.svg');
    }
}

@theme: default;