/* Устанавливает стандартную ширину, высоту и отступы элементов форм
 * По умолчанию отступы со всех сторон одинаковые */
/* Устанавливает отступы слева и справа */
.pro-data-list {
  --pro-data-list-group-height: 3.23076923rem;
  --pro-data-list-group-padding-x: 1.15384615rem;
  text-indent: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  border: 0;
  border: none;
}
.pro-data-list:not(:first-child) {
  border-top: 1px solid #ededed;
  padding-top: 0.76923077rem;
  margin-top: 0.76923077rem;
}
.pro-data-list--sm {
  --pro-data-list-group-height: 2.15384615rem;
  --pro-data-list-group-padding-x: 0.76923077rem;
}
.pro-data-list[data-group]:not([data-group='']) {
  padding-top: 0;
}
.pro-data-list[data-group]:not([data-group=''])::before {
  --box-out-height: var(--pro-data-list-group-height);
  --box-padding-x: var(--pro-data-list-group-padding-x);
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  content: attr(data-group);
  height: auto;
  display: block;
  font-weight: 700;
}
.pro-data-list__option {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  cursor: pointer;
}
.pro-data-list__option:hover {
  background: #f5f5f5;
}
.pro-data-list__option--autoheight {
  white-space: normal;
}
.pro-data-list__option--selected,
.pro-data-list__option--selected:hover {
  background: #f1f1f1;
}
/*
    Добавляет скругление углов первому и последнему соседнему элементу.
*/
/* 
    Спрямляет углы соседних элементов в местах их соприкосновения.
*/
/*
    Убирает вертикальные границы соседних элементов.
*/
/* Обратная совместимость. */
.pro-select__option {
  --box-padding-x: var(--pro-select-padding-x);
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  border: solid transparent;
  border-width: var(--pro-select-option-border-width, 0) 0;
}
.pro-select__option--autoheight {
  height: auto !important;
  white-space: normal;
}
.pro-select__option-emph {
  font-weight: 700;
  color: #2b2b2b;
}
.pro-select .pro-select__placeholder:not(:last-child) .pro-select__option,
.pro-select--mobile .pro-select__placeholder:not(:last-child) .pro-select__option {
  padding-right: 0;
}
.pro-select__checkbox-option {
  padding-left: calc(3rem - 15px + var(--pro-select-padding-x, 15px));
}
.pro-select__color-option {
  --box-padding-x: var(--pro-select-padding-x);
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 2.15384615rem) - var(--box-in-height, 1.46153846rem)) / 2));
  position: relative;
  display: flex;
  align-items: center;
  border: solid transparent;
  border-width: var(--pro-select-option-border-width, 0) 0;
}
.pro-select__color-option > :first-child {
  position: absolute;
  width: 1.84615385rem;
  height: 1.84615385rem;
  box-sizing: border-box;
  border: 2px solid #ffffff;
  outline: 1px solid #ededed;
}
.pro-select__color-option > :last-child {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-left: 2.61538462rem;
}
.pro-select__icon-option {
  --box-out-height: var(--pro-select-height);
  --box-padding-x: var(--pro-select-padding-x);
  box-sizing: content-box;
  height: var(--box-in-height, 1.84615385rem);
  line-height: var(--box-in-height, 1.84615385rem);
  min-height: var(--box-in-height, 1.84615385rem);
  min-width: var(--box-in-height, 1.84615385rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.84615385rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.84615385rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.84615385rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.84615385rem)) / 2));
  border: solid transparent;
  border-width: var(--pro-select-option-border-width, 0) 0;
}
/* Многострочная опция */
.pro-select__multiline-option {
  height: auto;
  white-space: normal;
}
.pro-select.pro-select--mobile .pro-select__multiline-option {
  height: auto;
}
/* Селект с запчастями */
/* TODO: нужен ли, или устарел после редизйна складов? */
.pro-select__part-option {
  height: 3.53846154rem;
  display: flex;
}
.pro-select__part-option .part-option__info {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 1rem;
  line-height: 1rem;
  color: #7b7b7b;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.pro-select__part-option .part-option__info > div {
  color: #3b3b3b;
}
.pro-select__part-option .part-option__image {
  min-width: 64px;
  height: 3.38461538rem;
  border: 1px solid #ededed;
  margin-right: 10px;
  box-sizing: content-box;
}
.pro-select__part-option .part-option__image > div {
  background-position: center center;
  background-size: cover;
  background-origin: content-box;
  border: 2px solid #ffffff;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
}
.pro-select__options {
  min-width: 100%;
  box-sizing: border-box;
  overflow: hidden;
  color: var(--pro-select-options-color);
}
.pro-select__options__bg {
  border-radius: 3px;
  background-color: #ffffff;
}
.pro-select__options__overflow {
  /* При высоте 1.5rem это шесть опций: */
  max-height: calc(9rem + 63px);
  overflow-y: auto;
  overflow-x: hidden;
  box-sizing: border-box;
}
.pro-select__options__overflow:not(:first-child) {
  padding-top: 12px;
  border-top: 1px solid #ededed;
}
.pro-select__options--hidden {
  max-height: 0;
  visibility: hidden;
}
.pro-select__options--dropdown {
  position: absolute;
  min-width: auto;
  left: -6px;
  right: -6px;
  top: calc(100% + 1px);
  z-index: 1;
}
.pro-select__options--dropdown .pro-select__options__bg {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  margin: 0 5px 5px;
}
.pro-select {
  --pro-select-height: 3.23076923rem;
  --pro-select-padding-x: 1.15384615rem;
  --pro-select-option-border-width: 1px;
  --pro-select-placeholder-color: var(--pro-form-control-color, #3b3b3b);
  --pro-select-options-color: #3b3b3b;
  position: relative;
  display: flex;
  flex-direction: column;
  flex: 1 1 auto;
  font-size: 1rem;
  min-height: var(--pro-select-height);
  /* Важно! Помогает обрезать текст, см. https://css-tricks.com/flexbox-truncated-text/ */
  min-width: 0;
  color: #7b7b7b;
  background: #ffffff;
  user-select: none;
}
.pro-select--sm {
  --pro-select-height: 2.15384615rem;
  --pro-select-padding-x: 0.76923077rem;
}
.pro-select--list {
  height: auto;
}
.pro-select--framed {
  border-width: var(--bordering-border-width, 1px);
  border-style: var(--bordering-border-style, solid);
  border-color: var(--bordering-border-color, #d8d8d8);
  border-radius: var(--bordering-border-radius, 3px);
  box-shadow: none;
}
.pro-select--framed:focus,
.pro-select--framed:focus-within {
  border-color: var(--bordering-border-focus-color, var(--bordering-border-color, #a6a6a6));
}
.pro-select--disabled {
  --pro-select-placeholder-color: inherit;
  --pro-select-options-color: inherit;
  color: #a6a6a6;
  background: #ededed;
  pointer-events: none;
}
.pro-select--transparent,
.pro-select--framed.pro-select--transparent {
  background: none;
  border-color: transparent;
}
.pro-select:focus,
.pro-select :focus {
  outline: none;
}
.pro-select[data-value=''] {
  --pro-select-placeholder-color: var(--pro-form-control-color, inherit);
}
.pro-select__group {
  position: relative;
  display: flex;
  flex-grow: 1;
  width: 100%;
}
/* Двухклассовый селектор нужен только чтобы перебить Bootstrap. Удалить после откл. старых стилей */
.pro-select .pro-select__search,
.pro-select .pro-select__placeholder {
  flex: 1 1 auto;
  margin: 0;
  font-family: inherit;
}
.pro-select .pro-select__search:first-child,
.pro-select .pro-select__placeholder:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.pro-select .pro-select__search:last-child,
.pro-select .pro-select__placeholder:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.pro-select .pro-select__search {
  --box-out-height: var(--pro-select-height);
  --box-padding-x: var(--pro-select-padding-x);
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  display: block;
  width: 1%;
  appearance: none;
  color: #3b3b3b;
  /* Перебить Bootstrap */
  border: none !important;
  box-shadow: none !important;
  transition: none !important;
}
.pro-select .pro-select__placeholder {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
  --pro-select-option-border-width: 0;
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
  align-items: center;
  padding: 0;
  color: var(--pro-select-placeholder-color);
  cursor: default;
}
.pro-select__toggle {
  display: flex;
  flex: 0 0 auto;
  align-items: center;
  width: 1.84615385rem;
  height: 1.84615385rem;
  padding: calc((var(--pro-select-height) - 1.84615385rem) / 2);
  border: none;
  box-sizing: content-box;
  text-align: center;
  white-space: nowrap;
  color: inherit;
  cursor: pointer;
}
/*
    TODO: стиль "уменьшенной" toggle-кнопки был создан раньше,
    чем появилась "sm"-модификация селекта и не особо согласуется с ней.
    Следует удалить этот модификатор, заменив его на свойство --pro-select-toggle-width,
    или что-то подобное для произвольной модификации размера кнопки.
*/
