  padding-left: 0.38461538rem;
  padding-right: 0.38461538rem;
}
/* TODO: этот класс нужен только на Автопро, отсюда его нужно убрать */
.pro-select__numbers--highlight {
  font-weight: bold;
}
.pro-select__numbers span {
  color: #7b7b7b;
}
.pro-select__placeholder .pro-select__numbers {
  font-weight: 400;
}
.pro-select__checkbox-wrapper {
  display: flex;
  flex-wrap: wrap;
  gap: 1px;
  padding: 10px var(--pro-select-padding-x) 12px;
  background: #ffffff;
}
.pro-select__checkbox-wrapper a {
  width: 100%;
  margin-top: 10px;
}
.pro-select .pro-label-block {
  --box-out-height: calc(var(--pro-select-height) - 2px);
}
.pro-select.pro-select--opened .pro-select__toggle__arrow {
  transform: rotate(180deg);
}
