/* Стандартная кнопка серого цвета */
.pro-btn {
  --pro-btn-out-height: 3.23076923rem;
  --pro-btn-padding-x: 1.53846154rem;
  --pro-btn-font-size: 1.07692308rem;
  --pro-btn-in-height: 1.69230769rem;
  --box-in-height: var(--pro-btn-in-height);
  --box-out-height: var(--pro-btn-out-height);
  box-sizing: content-box;
  height: var(--box-in-height, 1.46153846rem);
  line-height: var(--box-in-height, 1.46153846rem);
  min-height: var(--box-in-height, 1.46153846rem);
  min-width: var(--box-in-height, 1.46153846rem);
  padding-top: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-bottom: var(--box-padding-y, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-left: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  padding-right: var(--box-padding-x, calc((var(--box-out-height, 3.23076923rem) - var(--box-in-height, 1.46153846rem)) / 2));
  font-size: var(--pro-btn-font-size, 1rem);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  white-space: nowrap;
  vertical-align: middle;
  user-select: none;
  border: none;
  cursor: pointer;
  overflow: hidden;
  -webkit-appearance: none;
  font-family: inherit;
  font-weight: 500;
}
.pro-btn:not(.pro-btn--square) {
  --box-padding-x: var(--pro-btn-padding-x);
}
.pro-btn[disabled] {
  cursor: default;
}
a.pro-btn,
.pro-btn {
  color: #7b7b7b;
  background: #ededed;
  border-color: transparent;
}
a.pro-btn:hover,
.pro-btn:hover {
  color: #7b7b7b;
  background: #d8d8d8;
  border-color: transparent;
}
a.pro-btn:active,
.pro-btn:active {
  color: #7b7b7b;
  background: #c8c8c8;
  border-color: transparent;
}
a.pro-btn:disabled,
.pro-btn:disabled,
a.pro-btn[disabled],
.pro-btn[disabled] {
  color: #c8c8c8;
  background: #ededed;
  border-color: transparent;
}
.pro-btn:focus {
  outline: none;
  box-shadow: 0 0 0 1px #ffffff, 0 0 4px 1px #418aca;
}
.pro-btn--sm,
.pro-btn-group--sm .pro-btn {
  --pro-btn-font-size: 1rem;
  --pro-btn-in-height: 1.53846154rem;
  --pro-btn-out-height: 2.15384615rem;
  --pro-btn-padding-x: 1.15384615rem;
}
.pro-btn--framed {
  border: 1px solid transparent;
  border-radius: 3px;
}
.pro-btn--uppercase {
  --pro-btn-font-size: 0.92307692rem;
  --pro-btn-in-height: 1.38461538rem;
  font-weight: 900;
  text-transform: uppercase;
}
.pro-btn--uppercase.pro-btn--sm,
.pro-btn--uppercase.pro-btn-group--sm .pro-btn {
  --pro-btn-font-size: 0.76923077rem;
  --pro-btn-in-height: 1.07692308rem;
}
