/*
    Добавляет скругление углов первому и последнему соседнему элементу.
*/
/* 
    Спрямляет углы соседних элементов в местах их соприкосновения.
*/
/*
    Убирает вертикальные границы соседних элементов.
*/
/* Обратная совместимость. */
.pro-btn-group {
  display: inline-flex;
}
.pro-btn-group .pro-btn:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.pro-btn-group .pro-btn:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.pro-btn-group .pro-btn:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.pro-btn-group .pro-btn:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.pro-btn-group .pro-btn:not(:first-child) {
  border-left: none;
}
.pro-btn-group .pro-btn:not(:last-child) {
  border-right: none;
}
.pro-btn-group .pro-btn-square:not(:first-child) {
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
}
.pro-btn-group .pro-btn-square:not(:last-child) {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
}
.pro-btn-group .pro-btn-square:first-child {
  border-top-left-radius: 3px;
  border-bottom-left-radius: 3px;
}
.pro-btn-group .pro-btn-square:last-child {
  border-top-right-radius: 3px;
  border-bottom-right-radius: 3px;
}
.pro-btn-group .pro-btn-square:first-child:not(:last-child) {
  border-right: none;
  margin-right: calc(-1 * var(--border-width, 0));
}
.pro-btn-group button,
.pro-btn-group .pro-btn:focus {
  z-index: 1;
