@import '~@avtopro/style/mixins/font.less';
@import '~@avtopro/style/mixins/bordering.less';
@import '~@avtopro/style/mixins/glue.less';

.pro-input-mixin(@fontSize: @baseFontSize) {
    .fontSize(@fontSize);

    display: block;
    flex: 1 1 auto;

    /* Баг с этим width: auto - поле поиска не вписывается в 320px */
    // width: auto; // Для снятия ограничения по ширине */
    // min-width: 5em; // TODO: моб. вид полей с prepend и append */
    margin: 0;
    border: none;
    overflow: hidden;
    color: var(--pro-form-control-color, @blackLightColor);
    background-color: @whiteColor;
    outline: 0;

    &:disabled {
        background-color: @whiteDarkestColor;
    }

    &::placeholder {
        color: var(--pro-form-control-color, @grayColor);
    }

    &--framed {
        .bordering();
        .glue();
    }

    &:focus {
        outline: none;
    }
}

@theme: default;