@import '~@avtopro/style';
@import '~@avtopro/style/mixins/font.less';
@import '~@avtopro/style/mixins/box.less';
@import '~@avtopro/style/mixins/nowrap.less';
@import '~@avtopro/style/mixins/bordering.less';
@import '~@avtopro/style/src/pro-icon/index.less';

.pro-label-block {
    --label-icon-size: unit(19 / @baseFontSize, rem);
    --box-out-height: unit(@baseInputHeight / @baseFontSize, rem);
    --box-padding-x: unit(10 / @baseFontSize, rem);
    .box();
    .nowrap();
    .bordering(@focus: false; @borderWidth: 0; @borderStyle: solid; @borderColor: transparent);

    display: flex;
    gap: var(--box-padding-x);
    background-color: @whiteDarkerColor;

    &--sm {
        --label-icon-size: unit(16 / @baseFontSize, rem);
        --box-out-height: unit(@compactInputHeight / @baseFontSize, rem);
        --box-padding-x: unit(8 / @baseFontSize, rem);
    }

    .pro-label-block__icon {
        --pro-icon-width: var(--label-icon-size);
        --pro-icon-inner-size: var(--label-icon-size);
        .pro-icon-mixin(@height: 100%);

        color: @grayColor;
        cursor: pointer;
    }

    .pro-label-block__icon:hover svg {
        fill: @grayLighterColor;
    }
}

@theme: default;