@import '../../themes/index.less';

@proIconSize: unit(@baseIconHeight / @baseFontSize, rem);

.pro-icon-mixin(@width: @proIconSize; @height: @proIconSize; @iconSize: @proIconSize) {
    @proIconWidth: var(
        --pro-icon-width,
        @width
    );
    @proIconHeight: var(
        --pro-icon-height,
        @height
    );
    @proIconInnerSize: var(
        --pro-icon-inner-size,
        @iconSize
    );

    position: relative;
    display: inline-flex;
    width: @proIconWidth;
    height: @proIconHeight;

    svg {
        width: @proIconInnerSize;
        height: @proIconInnerSize;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: calc(@proIconInnerSize / -2);
        margin-left: calc(@proIconInnerSize / -2);
        fill: currentColor;
    }

    &--spin svg {
        animation: spin 2s linear infinite;
    }
}

@keyframes spin {
    100% {
        transform: rotate(360deg);
    }
}

.pro-icon {
    .pro-icon-mixin(100%; 100%);
}

.pro-icon-block {
    .pro-icon-mixin();
    display: block;
    transition: opacity 0.3s ease-in;

    &--muted {
        opacity: 0.5;
        cursor: default;
    }
}

.pro-icon-inline {
    .pro-icon-mixin(@proIconSize; 1em);
    vertical-align: middle;

    &--top {
        vertical-align: text-top;
    }
}

@theme: default;