@import '../mixins.less';

/* Стандартная кнопка серого цвета */
.pro-btn {
    --pro-btn-out-height: unit(@baseInputHeight / @baseFontSize, rem);
    --pro-btn-padding-x: unit(20 / @baseFontSize, rem);

    .buttonFontSize(@baseFontSize + 1);
    .pro-btn-mixin();

    .colorizeButton(@normal: {
      color: @grayColor; background: @whiteDarkestColor; border-color: transparent
    };

    @hover: {
      color: @grayColor; background: @grayLightestColor; border-color: transparent
    };

    @active: {
      color: @grayColor; background: @grayLighterColor; border-color: transparent
    };

    @disabled: {
      color: @grayLighterColor; background: @whiteDarkestColor; border-color: transparent
    };
  );

    font-family: inherit;
    font-weight: 500;

    &:focus {
        outline: none;
        box-shadow: 0 0 0 1px @whiteColor, 0 0 4px 1px @blueLighterColor;
    }
}

.pro-btn--sm {
    .buttonFontSize(@baseFontSize);
    --pro-btn-out-height: unit(@compactInputHeight / @baseFontSize, rem);
    --pro-btn-padding-x: unit(15 / @baseFontSize, rem);
}

.pro-btn--framed {
    border: 1px solid transparent;
    border-radius: @borderRadius;
}

.pro-btn--uppercase {
    .buttonFontSize(@baseFontSize - 1);
    font-weight: 900;
    text-transform: uppercase;
}

.pro-btn--uppercase.pro-btn--sm {
    .buttonFontSize(@baseFontSize - 3);
}

/* Главная кнопка зелёного цвета */
.pro-btn--prime {
    &:focus {
        color: @whiteColor;
        background: @paleGreenColor;
    }

    .colorizeButton(@normal: {
      color: @whiteColor; background: @greenLighterColor; border-color: transparent
    };
    @hover: {
      color: @whiteColor; background: @greenLightColor; border-color: transparent
    };
    @active: {
      color: @whiteColor; background: @paleGreenColor; border-color: transparent
    };
    @disabled: {
      color: @whiteColor; background: @greenLightestColor; border-color: transparent
    };
  );
}

/* Кнопка тёмно-синего цвета */
.pro-btn--blue {
    .colorizeButton(@normal: {
      color: @whiteColor; background: @blueLighterColor; border-color: transparent
    };
    @hover: {
      color: @whiteColor; background: @blueLightColor; border-color: transparent
    };
    @active: {
      color: @whiteColor; background: @blueColor; border-color: transparent
    };
    @disabled: {
      color: @whiteColor; background: @blueLightestColor; border-color: transparent
    };
  );
}

/* Кнопка светло-синего цвета */
.pro-btn--light-blue {
    .colorizeButton(@normal: {
      color: @blueLighterColor; background: @skyBlueLighterColor; border-color: transparent
    };
    @hover: {
      color: @blueLighterColor; background: @skyBlueLightColor; border-color: transparent
    };
    @active: {
      color: @blueLighterColor; background: @skyBlueColor; border-color: transparent
    };
    @disabled: {
      color: @blueLightestColor; background: @skyBlueLighterColor; border-color: transparent
    };
  );
}

/* Белая кнопка для элементов управления формами */
.pro-btn--white {
    .colorizeButton(@normal: {
      color: @grayLighterColor; background: @whiteColor; border-color: transparent
    };
    @hover: {
      color: @grayLighterColor; background: @whiteColor; border-color: transparent
    };
    @active: {
      color: @grayLighterColor; background: @whiteColor; border-color: transparent
    };
    @disabled: {
      color: @grayColor; background: @whiteColor; border-color: transparent
    };
  );
}

/* Белая кнопка с красным текстом по наведению (для кнопок удаления) */
.pro-btn--danger {
    .colorizeButton(@normal: {
      color: @redColor; background: none; border-color: transparent
    };
    @hover: {
      color: @redColor; background: none; border-color: transparent
    };
    @active: {
      color: @redColor; background: none; border-color: transparent
    };
    @disabled: {
      color: @grayColor; background: none; border-color: transparent
    };
  );

    // Исключение для кнопки danger связано с тем,
    // что в модалах такие кнопки иногда выносятся на поля
    // рядом с элементами, которые ими удаляются, и на полях
    // обводка кнопок отключена, поэтому нужно подсвечивать
    // сфокусированную кнопку другим способом, в данном случае цветом
    &:focus {
        color: @redColor;
    }
}

/* Кнопка с прозрачным фоном, которая выглядит как ссылка */
.pro-btn--link {
    font-weight: 400;
    .colorizeButton(@normal: {
        color: @blueLighterColor; background: none; border-color: transparent
      };
      @hover: {
        color: @grayColor; background: none; border-color: transparent
      };
      @active: {
        color: @grayColor; background: none; border-color: transparent
      };
      @disabled: {
        color: @grayColor; background: none; border-color: transparent
      };
    );

    &:focus {
        outline: 0;
        box-shadow: none;
    }
}

/*Красная кнопка с белым текстом (в стилизации youtube)*/

.pro-btn--youtube {
    .colorizeButton(@normal: {
      color: @whiteColor; background: @redDarkColor; border-color: transparent
    };
    @hover: {
      color: @whiteColor; background: @redDarkerColor; border-color: transparent
    };
    @active: {
      color: @whiteColor; background: @redDarkestColor; border-color: transparent
    };
    @disabled: {
      color: @whiteColor; background: @redColor; border-color: transparent
    };
  );

    &:focus {
        box-shadow: 0 0 0 1px @whiteColor, 0 0 4px 1px @redDarkColor;
    }
}

/* Кнопка в стилистике Telegram Messenger */
.pro-btn--telegram {
    .colorizeButton(@normal: {
        color: @whiteColor; background: #28a7e8; border-color: transparent
      };
      @hover: {
        color: @whiteColor; background: #1e84b8; border-color: transparent
      };
      @active: {
        color: @whiteColor; background: darken(#1e84b8, 5%); border-color: transparent
      };
      @disabled: {
        color: @whiteColor; background: lighten(#28a7e8, 5%); border-color: transparent
      };
    );

    &:focus {
        box-shadow: 0 0 0 1px @whiteColor, 0 0 4px 1px #28a7e8;
    }
}

@theme: default;