@import '~@avtopro/style/mixins/glue.less';

.pro-btn-group {
    display: inline-flex;

    .pro-btn {
        .glue(border-radius-reset);
        .glue(border-radius);
        .glue(border);
    }

    .pro-btn-square {
        .glue(border-radius-reset);
        .glue(border-radius);

        &:first-child:not(:last-child) {
            border-right: none;
            margin-right: calc(-1 * var(--border-width, 0));
        }
    }

    &--sm {
        .pro-btn {
            &:extend(.pro-btn--sm all);
        }
    }

    button,
    .pro-btn:focus {
        z-index: 1;
    }
}

@theme: default;