@import './option.less';
@import './checkbox-option.less';
@import './color-option.less';
@import './icon-option.less';
@import './multiline-option.less';
@import './part-option.less';

.pro-select__options {
    min-width: 100%;
    box-sizing: border-box;
    overflow: hidden;
    color: var(--pro-select-options-color);

    &__bg {
        border-radius: @borderRadius;
        background-color: @whiteColor;
    }

    &__overflow {
        /* При высоте 1.5rem это шесть опций: */
        max-height: calc(~'9rem + 63px');
        overflow-y: auto;
        overflow-x: hidden;
        box-sizing: border-box;

        &:not(:first-child) {
            padding-top: 12px;
            border-top: 1px solid @whiteDarkestColor;
        }
    }

    &--hidden {
        max-height: 0;
        visibility: hidden;
    }

    &--dropdown {
        position: absolute;
        min-width: auto;
        left: -6px;
        right: -6px;
        top: calc(100% + 1px);
        z-index: 1;
    }

    &--dropdown &__bg {
        box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
        margin: 0 5px 5px;
    }
}

@theme: default;