@import '~@avtopro/style';

.pro-loader {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: url("~@avtopro/icons/dist/svg/gears.svg") 50% no-repeat;
    background-color: rgba(255, 255, 255, 0.7);
    background-size: 80px 80px;
    z-index: 100;
  }
  
  body > .pro-loader {
    position: fixed;
    z-index: @zIndexOverlay + 10;
  }
@theme: default;