.reset-button() {
    background: none;
    border: 0;
    font: inherit;
    line-height: inherit;
    vertical-align: initial;
    overflow: visible;
    padding: 0;
    appearance: none;
}

.reset-ul() {
    text-indent: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    border: 0;
}

@theme: default;